<template>
  <v-card flat>
    <table>
      <tbody>
        <tr>
          <td style="width: 80px">
            <a :href="src">
              <v-btn
                v-if="src"
                icon
              >
                <v-icon large color="#900"> $pdf </v-icon>
              </v-btn>
            </a>
          </td>
          <td>
            <v-file-input
              v-model="file"
              :label="label"
              prepend-icon="mdi-file-upload"
              outlined
              color="#09b"
            >
              <template v-slot:selection="{ text }">
                <v-chip
                  small
                  label
                  color="#09b"
                >
                  {{ text }}
                </v-chip>
              </template>
            </v-file-input>
          </td>
        </tr>
      </tbody>
    </table>
    <iframe :src="src" width="100%" height="500px"></iframe>
  </v-card>
</template>

<script>

export default {
  props: {
    label: String,
    path: String,
    src: String
  },
  data: () => ({
    file: null
  }),
  computed: {
    error () {
      return Boolean(this.setHint())
    }
  },
  watch: {
    file: {
      deep: true,
      handler (val) {
        if (!val) return
        this.upload()
      }
    }
  },
  methods: {
    showPDF () {
      window.open(this.src, '_blank')
    },
    async upload () {
      const buildingId = localStorage.getItemByName('selectedBuilding')._id
      console.log(buildingId)
      const routes = require('@/config/uploadPDF').default
      console.log(this.path, routes[this.path])
      if (!routes[this.path]) throw new Error('Invalid path for file upload')
      let response = await this._uploadBuildingPDF(this.file, this.path)
      console.log(response)
      response = await this._getBuildingDetails(buildingId)
      console.log(response)
    }
  }
}
</script>
